import {mapState,mapMutations} from 'vuex';
export default {
	data() {
		return {};
	},
	computed:{
		...mapState(['app']),
	},
	methods: {
		...mapMutations(['urlRequest']),
		choose(row) {
			console.log(row)
			this.urlRequest({
				method:"post",
				url:`/user-service/user/getUserInAllianceResouses?access_token=${this.app.access_token}`,
				data:{
					params: {allianceId:row }
				},
				callBack:res=>{
					console.log(res)
					uni.reLaunch({
						url: '../home/home'
					});
				}
			})
		}
	}
};
